.cont{
    width: 90%;
    justify-self: center;
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.cont>h1{
    width: fit-content;
    font-size: 3em;
    position: relative;
}

.cont>h1::before{
    content: " ";
    bottom: 0;
    right: 0;
    height: 3px;
    width: 30%;
    background-color: #fff;
    position: absolute;
}

.cont article{
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.title{
    font-size: 2em;
}

.cont img{
    padding: 10px;
    border: 2px solid #3e3e3e;
    background-color: #1d1d1d;
    border-radius: 5px;
    width: 100%;
}

.link{
    font-size: 1em;
    color: #fff;
}

.PF-box{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.PF{
    padding: 15px 40px;
    border: 2px solid #3e3e3e;
    border-radius: 5px;
    font-size: 1em;
    font-weight: 400;
}

.lang-box{
    align-self: center;
    width: fit-content;
    border: 2px solid #ab68d8;
    border-radius: 15px;
    display: flex;
    flex-wrap: wrap;
    padding: 20px;
    gap: 20px;
}

.lang-box>h4{
    color: #ab68d8;
    font-size: 1.25em;
    font-weight: 400;
}