.cont{
    display: grid;
    gap: 20px;
    font-family: "Roboto", sans-serif;
    color: #fff;
}

.preview{
    display: grid;
    justify-content: center;
    text-align: center;
}

.preview>*{
    color: #fff;
}

.preview>h1{
    font-size: 4em;
}

.preview>p{
    font-size: 2em;
    font-weight: 400;
}

.preview>p:nth-child(3){
    font-size: 1.5em;
    color: #727272;
}

.skills{
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 95%;
    justify-self: center;
}

.skills>h1{
    width: fit-content;
    font-size: 3em;
    position: relative;
}

.skills>h1::before{
    content: " ";
    bottom: 0;
    right: 0;
    height: 3px;
    width: 30%;
    background-color: #fff;
    position: absolute;
}

.skills>div{
    display: grid;
    grid-template: repeat(auto-fill, 150px) / repeat(auto-fill, 250px);
    justify-content: space-between;
    gap: 20px;
}

.skills article{
    height: 150px;
    width: 250px;
    display: grid;
    grid-template: 70px / 100px;
    text-align: center;
    justify-items: center;
    justify-content: center;
    align-content: center;
    border: 2px solid #3e3e3e;
    border-radius: 15px;
    position: relative;
    overflow: hidden;
}

.skills img{
    height: 100%;
    width: 70px;
}

.skills h5{
    top: 15px;
    position: absolute;
    font-family: "Black";
    font-weight: 400;
    font-size: 6em;
    color: #3e3e3e;
    z-index: -1;
    line-height: 120px;
}

.links-cont{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.links-cont>a{
    color: #727272;
}

@media (max-width: 800px){
    .skills>div{
        justify-content: space-evenly;
    }
}