@font-face {
    font-family: "Black";
    src: url("././static/fonts/lovelolinelight.ttf");
}

@font-face {
    font-family: "BlackCraft";
    src: url("././static/fonts/Blackcraft.ttf");
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a{
    text-decoration: none;
}

div{
    cursor: default;
}

body{
    display: flex;
    flex-direction: column;
    background-color: #000000;
    overflow: hidden;
    transition: opacity 1s linear;
}

@media (max-width: 1280px){
    body{
        overflow: auto;
    }
}