.cont{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 15px;
    position: absolute;
    z-index: 3;
    transition: all .35s linear;
}

.box{
    transition: all .4s linear;
}

.card{
    height: 100%;
    width: 100%;
    gap: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.title{
    display: flex;
    align-items: center;
    font-family: "Roboto", sans-serif;
    font-size: 3em;
    font-weight: 700;
    letter-spacing: 3px;
    color: #fff;
    filter: drop-shadow(5px 5px 1px #727272);
}

.img-cont{
    width: 60%;
    padding: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 25px;
    background-color: rgba(150, 150, 150, 0.15);
    backdrop-filter: blur(5px);
    position: relative;
}

.img{
    border-radius: 10px;
    height: 100%;
    width: 100%;
    filter: brightness(0.8);
    transition: all .3s linear;
}

.link{
    opacity: 0;
    left: calc(50% - 50px);
    height: 100px;
    width: 100px;
    border: 2px solid transparent;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.8);
    transition: all .3s linear;
    color: #fff;
}

.link:hover{
    border-color: #ca9ee8
}

.img-cont:hover .link{
    opacity: 1;
}

.img-cont:hover .img{
    filter: brightness(0.5);
}

.link:hover::before{
    border-left-color: #727272;
}

.PF{
    width: 40%;
    display: flex;
    flex-direction: column;
    font-family: "Roboto", sans-serif;
    color: #fff;
}

.PF>h2{
    font-size: 1.25em;
    line-height: 20px;
    color: #fff;
}

.PF>div{
    padding: 10px;
    max-width: 100%;
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
}

.PF>div::-webkit-scrollbar{
    background-color: #696969;
}

.PF>div::-webkit-scrollbar-thumb{
    background-color: #ab68d8;
}

.PF p{
    padding: 10px 30px;
    height: 40px;
    border: 2px solid #727272;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.875em;
}

.hrefs-cont{
    padding: 20px;
    border: 2px solid rgba(81, 50, 102, 0.2);
    border-radius: 10px;
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: flex-start;
}

.href{
    width: fit-content;
    font-family: "Roboto", sans-serif;
    font-size: 1em;
    font-weight: 400;
    letter-spacing: 1px;
    line-height: 25px;
    color: #727272;
    text-align: left;
    position: relative;
    transition: all .4s linear;
    cursor: pointer;
}

.href::before{
    content: " ";
    left: 0;
    bottom: 0;
    height: 3px;
    width: 0%;
    background-color: #ca9ee8;
    position: absolute;
    transition: all .4s linear;
}

.href:hover::before{
    width: 100%;
}

.skills-cont{
    width: 100%;
    display: flex;
    gap: 20px;
    justify-content: center;
    align-content: center;
}

.skill{
    padding: 5px 10px;
    height: fit-content;
    border: 2px solid #ab68d8;
    border-radius: 10px;
    display: flex;
    align-items: center;
    font-family: "Roboto", sans-serif;
    font-size: 1em;
    font-weight: 400;
    letter-spacing: 1px;
    transition: all .4s linear;
    color: #ca9ee8;
    filter: drop-shadow(0px 0px 4px #ab68d8);
}

.skill:hover{
    border-color: transparent;
}

.img-none{
    width: 100%;
    font-family: "Roboto", sans-serif;
    font-size: 2em;
    font-weight: 500;
    text-align: center;
    letter-spacing: 3px;
    color: #727272;
}

.skill-none{
    width: 100%;
    font-family: "Roboto", sans-serif;
    font-size: 2em;
    font-weight: 500;
    writing-mode: vertical-rl;
    text-align: center;
    letter-spacing: 3px;
    color: #727272;
}

.desc-none{
    width: 100%;
    font-size: 2em;
    font-weight: 500;    
    font-family: "Roboto", sans-serif;
    text-align: center;
}